.slider-wrap {
  position: relative;
  margin-bottom: 15px;
  box-shadow: 0 4px 20px -4px rgba(0,0,0,0.2);
  .slider__item {
    display: block;
    overflow: hidden;
  }
  .slick-dots {
    position: absolute;
    right: 20px;
    bottom: 20px;
    display: flex;
    align-items: center;
    &:hover {
      cursor: pointer;
    }
    li {
      margin: 2px;
      display: block;
      width: 10px;
      height: 10px;
      border: 1px solid #FFF;
      background-color: #6c6d6f;
      &.slick-active {
        background-color: #0d2ea1;
      }
    }

    button {
      display: none;
    }
  }
  .slider-left,
  .slider-right {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 60px;
    z-index: 100;
    &:hover {
      cursor: pointer;
      svg {
        opacity: .8;
        //box-shadow: 4px 2px 8px -2px rgba(0,0,0,.3);
        filter: drop-shadow(0 0 3px rgba(100,0,0,0.5));
      }
    }
    svg {
      width: 130px;
      fill: white;
      opacity: .4;
      transition: all .4s ease;
      @media screen and (max-width: 768px) {
      	width: 60px;
      }

    }
  }

  .slider-left {
    left: 60px;
    transform: rotate(180deg) translateY(50%);
    @media screen and (max-width: 768px) {
      left: 20px;
    }

  }

  .slider-right {
    right: 60px;
    @media screen and (max-width: 768px) {
      right: 20px;
    }
  }
}

.slider-wrap .slider__item {
  vertical-align: top;
}
