.products-items {
  padding: 24px 20px 0;
  background: #fff;
  box-shadow: 0 4px 20px -4px rgba(0, 0, 0, 0.1);
  &.garment {
    .products-items__grid {
      grid-template-columns: repeat(5, 1fr);
      @media screen and (max-width: 992px) {
        grid-template-columns: repeat(4, 1fr);
      }
      @media screen and (max-width: 768px) {
      	grid-template-columns: repeat(3, 1fr);
      }

      @media screen and (max-width: 568px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media screen and (max-width: 500px) {
      	max-width: 206px;
      }
    }
  }
  &__block {
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 2px solid #E0E0E0;
    &:last-child {
      border-bottom: none;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    @media screen and (max-width: 992px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media screen and (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media screen and (max-width: 500px) {
      display: block;
      margin: 0 auto;
      max-width: 300px;
    }
  }

  &__header {
    margin-bottom: 15px;
    font-size: 18px;
    font-weight: bold;
  }

  &__item {
    padding: 5px;
    border: 1px solid #e5e5e5;
    box-shadow: 0 4px 10px -7px rgba(0,0,0,0.7);
    @media screen and (max-width: 500px) {
      margin-bottom: 30px;
    }

  }
  &__img {
    margin-bottom: 5px;
    border: 2px solid #FFF;
    box-shadow: 0 0 0 2px #e5e5e5;
  }
  &__content {
    display: flex;
    align-items: stretch;
    flex-direction: column;
    height: 128px;
  }
  &__title {
    a {
      display: block;
      font-size: 14px;
      font-weight: bold;
      text-decoration: none;
      color: #01478d;
    }
  }
  &__text {
    flex: 1;
    margin-bottom: 18px;
  }
  &__link {
    display: block;
    margin-top: auto;
    height: 36px;
    line-height: 36px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    background: linear-gradient(#0059AE, #033E80);
  }
}