.sitemap-content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  h2 {
    a {
      display: block;
      margin-bottom: 20px;
      font-size: 12px;
      font-weight: bold;
      text-decoration: none;
      text-transform: uppercase;
      color: #01478d;
    }
  }
  ul {
    li {
      position: relative;
      padding-left: 14px;
      &::before {
        content: '';
        position: absolute;
        top: 7px;
        left: 0;
        width: 4px;
        height: 4px;
        background: #888;
        border-radius: 50%;
      }
    }
  }
}