.navbar {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
  background: linear-gradient(#fefefe, #dadada);
  border: 1px solid #d4d4d4;
  border-radius: 8px;
  @media screen and (max-width: 992px) {
  	min-height: 34px;
  }

}
.main-menu {
  display: flex;
  align-items: center;
  @media screen and (max-width: 992px) {
    display: none;
    padding-top: 32px;
    width: 100%;
    text-align: center;
  }
  li.current-menu-item {
    a {
      color: #005cab;
    }
  }

  li:first-child {
    a {
      border-left: none;
      @media screen and (max-width: 992px) {
        border-top: 1px solid #dcdcdc;
      }
    }
  }
  a {
    display: block;
    @include helvetica;
    font-weight: bold;
    font-size: 12px;
    color: #282828;
    text-decoration: none;
    padding: 11px 35px 11px 35px;
    border-right: 1px solid #eaeaea;
    border-left: 1px solid #dcdcdc;
    @media screen and (max-width: 992px) {
      border: none;
      border-bottom: 1px solid #dcdcdc;
    }
    &:hover {
      color: #005cab;
    }
  }
}
.language {
  position: relative;
  z-index: 100;
  @media screen and (max-width: 992px) {
    position: absolute;
    top: 0;
    right: 0;
  }

  &__title {
    position: relative;
    display: block;
    padding: 10px 24px 10px 10px;
    font-family: 'Helvetica',sans-serif;
    font-size: 12px;
    font-weight: 700;
    color: #282828;
    text-decoration: none;
    border-left: 1px solid #dcdcdc;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 4px;
      transform: translateY(-50%);
      width: 8px;
      height: 4px;
      background-image: url("../i/language-arrow.png");
    }
    &.active {
      &::after {
        background-image: url("../i/language-arrow-hover.png");
      }
    }
  }
  &__list {
    position: absolute;
    top: 100%;
    left: 0;
    display: none;
    margin-top: -1px;
    width: 100%;
    background-color: #f0f0f0;
    border: 1px solid #cdcdcd;
    box-shadow: 3px 3px 6px 0 rgba(0, 0, 0, 0.4);
    li.active  a{
      color: #00529c;
      background-color: #dbd8d7;
    }
    a {
      display: block;
      padding: 5px;
      font-family: 'Helvetica',sans-serif;
      font-size: 12px;
      font-weight: normal;
      text-decoration: none;
      text-transform: uppercase;
      color: #282828;
      border-bottom: 1px solid #cdcdcd;
      &:hover {
        color: #00529c;
        background-color: #dbd8d7;
      }
    }
  }
}
