.banner {
  width: 190px;
  border: 1px solid #d9dce3;
  &__header {
    padding: 5px;
    font-weight: bold;
    color: white;
    background-color: #005cab;
  }
  &__content {
    padding: 5px;
    background: white;
  }

  p {
    margin-bottom: 10px;
  }
  table {
    border-collapse: collapse;
    border: 1px solid #d9dce3;
    td {
      padding: 3px;
      text-align: center;
      border: 1px solid #d9dce3;
      a {
        color: #01478d;;
        text-decoration: none;
      }
    }
  }
}