.blocks {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 18px;
  margin-bottom: 18px;
  @media screen and (max-width: 992px) {
    display: block;
  }
  &.two-blocks {
    grid-template-columns: repeat(2, 1fr);
  }
  a {
    @media screen and (max-width: 992px) {
      text-decoration: none;
    }

  }
  &__item {
    position: relative;
    box-shadow: 0 4px 20px -4px rgba(0,0,0,.2);
    overflow: hidden;
    @media screen and (max-width: 992px) {
      display: block;
      margin: 0 auto 30px;
      width: 400px;
    }
    @media screen and (max-width: 430px) {
      width: 280px;
    }


  }
  &__image-bg {
    background-size: cover;
    height: 286px;
  }
  &__content {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 6px 8px 15px;
    width: 100%;
    min-height: 100px;
    background: #192f7dbf;
    border-top: 1px solid #5E77B7;
    transition: all .2s ease;
    @media screen and (max-width: 992px) {
      position: static;
    }

  }
  &__content-header {
    display: flex;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #5E77B7;
    img {
      margin-right: 8px;
      @media screen and (max-width: 992px) {
        display: none;
      }

    }
  }
  &__brand-title {
    font-size: 12px;
    font-weight: bold;
    color: #FFF;
    text-transform: uppercase;
  }
  &__title {
    font-size: 18px;
    font-weight: normal;
    color: #a9d5ff;
    text-transform: uppercase;
  }
  &__image_hover {
    display: none;
  }
  &__text {
    padding-top: 15px;
    font-family: 'Arial', sans-serif;
    line-height: 18px;
    color: white;
  }
}
.blocks .blocks__brand-title {
  margin-bottom: 0;
}