.country {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #e0e0e0;
  &__title {
    margin-bottom: 5px;
    font-size: 14px;
    font-weight: bold;
    color: #000;
  }
  &__text {
    margin-bottom: 18px;
    max-width: 156px;
  }
  &__table {
    max-width: 180px;
    .tr {
      display: flex;
      justify-content: space-between;
      a {
        text-decoration: none;
        color: $link-color;
      }
      .td {
        &:first-child {
          font-weight: bold;
        }
        &:last-child {
          width: 106px;
        }
      }
    }
  }
  &__back-link {
    text-decoration: none;
    color: $link-color;
  }
}