.section {
  margin-bottom: 13px;
  padding: 30px 20px;
  background: white;
  box-shadow: 0 4px 20px -4px rgba(0, 0, 0, 0.1);
  &.section-country {
    box-shadow: none;
  }
  &.padding-right {
    padding-right: 52px;
  }
  &.grid {
    display: grid;
    grid-template-columns:  200px 1fr;
    grid-gap: 28px;
    @media screen and (max-width: 992px) {
      display: block;
      padding-right: 20px;
    }
    aside {
      @media screen and (max-width: 992px) {
        display: table;
        margin-bottom: 30px;
      }

    }

  }
  &__title {
    margin-bottom: 18px;
    padding-bottom: 7px;
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    color: #005cab;
    border-bottom: 1px solid #e0e0e0;
  }
}
.main-content {
  position: relative;
  h3 {
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: bold;
  }
  p {
    margin-bottom: 20px;
  }
  strong {
    font-weight: bold;
  }
 &--banner {
   padding-right: 210px;
   @media screen and (max-width: 992px) {
     padding-right: 0;
   }

 }
  .banner {
    position: absolute;
    top: -46px;
    right: 6px;
    @media screen and (max-width: 992px) {
      position: static;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -32px;
      width: 30px;
      height: 100%;
      background: white;
      @media screen and (max-width: 992px) {
        display: none;
      }

    }
  }
}
.main {
  &.press-releases {
    h2 {
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: bold;
    }
  }
  img {
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
    &.border {
      border: 2px solid #FFF;
      box-shadow: 0 0 0 2px #e5e5e5;
    }
  }
  hr {
    height: 1px;
    background: #e0e0e0;
    border: none;
  }
  h3 {
    margin-bottom: 15px;
    font-size: 14px;
    font-weight: bold;
    color: $title-color;
  }
  h4 {
    margin-bottom: 16px;
    font-weight: bold;
  }
  p {
    margin-bottom: 20px;
  }
  a {
    text-decoration: none;
    color: $link-color;
  }
  strong {
    font-weight: bold;
  }
  ul {
    margin-bottom: 20px;
    li {
      position: relative;
      padding-left: 14px;
      &::before {
        content: '';
        position: absolute;
        top: 7px;
        left: 0;
        width: 4px;
        height: 4px;
        background: #888;
        border-radius: 50%;
      }
    }
  }
  .table-adaptive {
    @media screen and (max-width: 600px) {
      overflow-x: scroll;
    }

  }
  table {
    border-collapse: collapse;
    border-spacing: 0;
    border: 1px solid #333;
    td {
      padding: 5px;
      text-align: center;
      color: #99999a;
      border: 1px solid #333;
      &:nth-child(4) {
        width: 80px;
      }
    }
  }
}
.page-image {
  display: block;
  margin-bottom: 14px;
}
a.pdf {
  i {
    margin-right: 5px;
  }
  span {
    font-weight: bold;
  }
}
.table-colorized {
  @media screen and (max-width: 600px) {
  	overflow-x: auto;
  }

  table {
    width: 100%;
    border-collapse: collapse;
    th {
      padding: 5px;
      background: $title-color;
      color: white;
      border: 1px solid #fff;
    }
    td {
      padding: 5px;
      border: 1px solid #d9dce3;
      a {
        font-weight: bold;
      }
    }
  }

}
.page-header {
  position: relative;
  time {
    position: absolute;
    top: 0;
    right: 6px;
  }
}
.clearfix {
  &::after {
    content: '';
    display: block;
    clear: both;
  }
}
