.newsletter-form {
  margin: 50px 0;
  .form-group {
    display: grid;
    grid-template-columns: 250px 1fr;
    border: 1px solid $alto;
    @media screen and (max-width: 530px) {
    	grid-template-columns: 90px 1fr;
    }

    &:first-child,
    &:nth-child(2) {
      border-bottom: none;
    }
  }
  .form-group__item {
    padding: 5px;
    &:first-child {
      padding-left: 30px;
      border-right: 1px solid $alto;
    }
    &:last-child {
      text-align: center;
    }
  }
  input[type="email"] {
    text-indent: 10px;
    outline: none;
    border: 1px solid $alto;
    @media screen and (max-width: 340px) {
    	width: 100%;
    }
  }
  input[type="submit"] {
    margin-top: 20px;
    padding: 10px 20px;
    min-width: 150px;
    color: rgb(255, 255, 255);
    background-color: rgb(0, 186, 255);
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  .checkox-row {
    display: flex;
    align-items: center;
    width: 100%;
    cursor: pointer;
    input {
      margin-right: 100px;
      @media screen and (max-width: 630px) {
        margin-right: auto;
      }
    }
  }
}