.main-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 15px;
  padding: 8px;
  background: linear-gradient(to right, $torea-bay, $gulf-blue);
  @media screen and (max-width: 400px) {
    display: block;
  }

  .logo {
    position: relative;
    left: 20px;
    @media screen and (max-width: 400px) {
      position: static;
      display: table;
      margin: 0 auto 20px;
      padding-top: 20px;
    }

    a {
      display: block;
    }
  }
  &__data {
    @media screen and (max-width: 400px) {
      position: static;
      display: table;
      margin: 0 auto;
    }
  }

  &__links {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    margin-bottom: 10px;

    li {
      &:first-child a {
        position: relative;

        &::after {
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          width: 1px;
          height: 10px;
          background: #838DAE;
        }
      }

      &:last-child a {
        padding-right: 0;
      }
    }

    a {
      padding: 0 10px;
      font-family: Arial;
      font-size: 10px;
      font-weight: bold;
      color: #ffffff;
      text-decoration: none;
    }
  }

  .search-form {
    position: relative;
    width: 150px;
    height: 17px;
    background: url("../i/searchbox_background.png");
    &.active {
      color: black;
      background-position: center bottom;
    }

    input {
      position: relative;
      top: -4px;
      padding: 0;
      width: 100%;
      height: 17px;
      text-indent: 5px;
      background: transparent;
      border: none;
    }
  }
}
