.breadcrumbs {
  display: flex;
  margin-bottom: 20px;
  margin-left: 13px;
  li {
    padding-left: 7px;
    padding-right: 13px;
    font-size: 11px;
    color: $text-color;
  }
  a {
    position: relative;
    color: $link-color;
    &::after {
      content: '>';
      position: absolute;
      right: -14px;
      top: -3px;
      color: $link-color;
    }
  }
}