.big-links {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 18px;
  margin-bottom: 25px;
  @media screen and (max-width: 768px) {
    display: block;
    margin: 0 auto;
    max-width: 300px;
  }
  &__item {
    position: relative;
    display: block;
    box-shadow: 0 4px 20px -4px rgba(0,0,0,.2);
    overflow: hidden;
    @media screen and (max-width: 768px) {
      margin-bottom: 18px;
    }
  }
  img {
    display: block;
  }
}


