.events {
  margin-bottom: 14px;
  &__line {
    height: 15px;
    background: linear-gradient(#E9E9E9 2%, #FDFDFD 20%, #EBEBEB);
    border: 1px solid #EBEBEB;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    &--bottom {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px;
    }
  }
  &__body {
    padding: 12px 20px 20px;
    background: #fff;
  }
  &__header {
    margin-bottom: 20px;
  }
  &__link {
    position: relative;
    padding-left: 12px;
    font-size: 18px;
    font-weight: bold;
    text-decoration: none;
    color: #01478d;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      border: 5px solid transparent;
      border-left-color: #ccc;
    }
  }
  &__content {
    display: flex;
    @media screen and (max-width: 768px) {
      display: block;
    }

  }
  &__img {
    display: block;
    margin-right: 10px;
    flex: 0 0 117px;
    border: 2px solid white;
    box-shadow: 0 0 0 2px #DBDFE9;
    @media screen and (max-width: 968px) {
    	display: table;
      margin-bottom: 10px;
    }

    img {
      display: block;
    }
  }
  &__item {
    display: flex;
    align-items: flex-start;
    padding-right: 60px;

    @media screen and (max-width: 968px) {
      display: block;
    }
    @media screen and (max-width: 768px) {
      margin-bottom: 30px;
      padding-right: 15px;
      max-width: 280px;
    }
  }
  &__time {
    font-size: 11px;
    line-height: 11px;
    color: #A7A7A7;
  }
  &__title a {
    display: block;
    margin-bottom: 13px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    color: #01478d;
  }
  &__text {
    font-size: 12px;
    line-height: 18px;
    color: #878686;
  }
}
