.sidebar {
  &__list {
    li.active {
      a {
        font-weight: bold;
        color: $link-color;
        &::before {
          background-image: url('../i/sidebar_navigation_active.png');
        }
      }
    }
    li:first-child {
      a {
        border-top: none;
      }
    }
    ul {
      display: none;
      padding: 5px 0 5px 20px;
      a {
        padding-top: 0;
        padding-bottom: 0;
        border: none;
        &::before {
          top: 5px;
        }
      }
    }
    a {
      position: relative;
      display: block;
      padding: 6px;
      padding-left: 10px;
      text-decoration: none;
      color: #7a7a7a;
      border-top: 1px solid #e0e0e0;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 11px;
        width: 5px;
        height: 7px;
        background: url("../i/sidebar_navigation_unactive.png");
        background-size: cover;
      }
    }
  }
}